.NeobisBigLogo {
    width: 28%;
}

.NeobisBigLogoContainer {
	top: 35vh;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
@media screen and (max-width: 950px) {
    .NeobisBigLogoContainer {
        top: 250px;
    }
}
@media screen and (max-width: 850px) {
    .NeobisBigLogoContainer {
        top: 190px;
    }
}
@media screen and (max-width: 700px) {
    .NeobisBigLogoContainer {
        top: 10vh;
    }
}

@media screen and (max-width: 555px) {
	.NeobisBigLogoContainer {
        top: 5vh;
    }
}


