.StepsSection {
    background: #21d38e; /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #21d38e 0%,#28c789 50%,#32b582 100%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, from(#21d38e),color-stop(50%, #28c789),to(#32b582));
    background: -o-linear-gradient(top, #21d38e 0%,#28c789 50%,#32b582 100%);
    background: linear-gradient(to bottom, #21d38e 0%,#28c789 50%,#32b582 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#21d38e', endColorstr='#32b582',GradientType=0 ); /* IE6-9 */

    -webkit-box-shadow: 0 0 0 24px rgba(204,241,227,1);
    box-shadow: 0 0 0 24px rgba(204,241,227,1);

    /*border-radius: 0 50% 50% 0;*/
    /*border-radius: 0% 70% 70% 0px / 0px 100% 100% 0px;*/
    border-radius: 0 52% 54% 0 / 0 100% 100% 0;
    margin-right: 15%;
    margin-top: 150px;
}

.StepsSectionContainer {
    /*margin-left: 24%;*/
    margin: 0 auto;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: 500px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

@media screen and (max-width: 870px) {
    .StepsSection {
        margin: 0;
    }
}

@media screen and (max-width: 740px) {
    .StepsSection {
        height: 700px;
    }
    .StepsSectionContainer {
        height: 100%;
        -ms-flex-pack: distribute;
            justify-content: space-around;
    }
}

@media screen and (max-width: 496px) {
    .StepsSection {
        height: 1000px;
    }
    .StepsSectionContainer {
        padding-top: 30px;
    }
}


