.SectionTitle h3 {
    font-size: 30px;
    font-family: "Raleway SemiBold", sans-serif;
    color: rgb(0, 0, 0);
    text-transform: uppercase;
    line-height: 1.933;
    text-align: center;
    z-index: 400;
    font-weight: 500;
    margin-top: 148px;
}

.SectionUnderline {
    border-bottom: 2px solid rgb(50, 180, 130);
    width: 54px;
    height: 1px;
    margin-top: -25px;
}

.SectionTitle {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

@media screen and (max-width: 1300px) {
    .SectionTitle h3 {
        margin-top: 100px !important;
    }
}



