.StepTitle {
    font-size: 24px;
    font-family: "Lato Bold", sans-serif;
    color: rgb(255, 255, 255);
    font-weight: bold;
    text-transform: uppercase;
    line-height: 2.417;
    text-align: center;
}

.StepBody {
    font-size: 15px;
    font-family: "OpenSans Regular", sans-serif;
    color: rgb(255, 255, 255);
    line-height: 1.294;
    text-align: center;
    position: absolute;
    width: 210px;
    margin: 0 auto;
    font-weight: 100;
}

.Step {
    margin: auto 15px;
    width: 210px;
    height: 300px;
}

.IconTitle {
    position: relative;
    margin-bottom: -4px;
}


.StepIcon img {
    border: 0.5px dashed white;
    border-radius: 50%;
}

.StepNumber {
    font-size: 36px;
    font-family: "Lato Bold", sans-serif;
    color: rgba(22, 93, 66, 0.302);
    font-weight: bold;
    line-height: 1.611;
    text-align: center;
    -moz-transform: matrix( 3.87580888355023,0,0,3.87580888355024,0,0);
    -webkit-transform: matrix( 3.87580888355023,0,0,3.87580888355024,0,0);
    -ms-transform: matrix( 3.87580888355023,0,0,3.87580888355024,0,0);
    position: absolute;
    right: 59px;
    top: -53px;
}

.StepIconDashedBorder {
    width: 67%;
    margin: 0 auto;
}



