    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-preferred-size: 29%;
        flex-basis: 29%;
    margin: 5px 5px;
}

.PlatformIcon p{
    /*width: 7%;*/
